.card {
    position: relative;
    background-color: whitesmoke;
    width: calc(100% - 4px);
    height: 130px;
    border-radius: 30px;
    border: 2px solid white;
    transition: all 0.5s ease;
    text-align: center;
    margin-top: 40px;
    color: rgb(186, 196, 252);
    border: solid 3px transparent;
    background-image: repeating-radial-gradient(
            circle farthest-corner at top center,
            #8ec5fc 0%,
            #e0c3fc 100%
        ),
        linear-gradient(101deg, #8ec5fc, #e0c3fc);
    background-origin: border-box;
    background-clip: content-box, border-box;
    box-shadow: 2px 1000px 1px #fff inset;
}

.card:hover {
    box-shadow: 2px 1000px 1px #fff inset, 3px 3px 10px #3d3d3d65;
}

.cardImage {
    position: absolute;
    z-index: 3;
    width: 100px;
    border-radius: 15px;
    height: 100px;
    top: calc((100% - 100px) / 2);
    left: 15px;
    object-fit: cover;
}

.cardTitle {
    position: absolute;
    font-family: "Helvetica";
    color: grey;
    z-index: 2;
    font-size: 28px;
    font-weight: bold;
    top: 30px;
    left: 130px;
}

.cardDescriptionDiv {
   position: absolute;
   z-index: 1;
   background-color: #ebebeb80;
   height: 30px;
   width: calc(100% - 145px);
   left: 15px;
   top: 15px;
   border-radius: 15px;
   padding-top: 55px;
   padding-left: 115px;
   padding-bottom: 15px;
   text-align: start;
   font-family: "Helvetica";
   color: black;
}

.cardNavigateImage {
    position: absolute;
    z-index: 3;
    top: calc((100% - 35px) / 2);
    right: 35px;
}